.contacts-container {
    width: calc(100% - 4rem);
    height: auto;
    position: relative;
    border-top-color: #5CE1E6;
    border-top-width: 4px;
    border-top-style: solid;
    left: 2rem;
    padding-top: 2rem;
    margin-bottom: 5rem;
    overflow: hidden;
}

.subtitle {
    font-family: "Montserrat", Arial, Helvetica, sans-serif;
    margin-bottom: 1rem;
    font-weight: 600;
    font-size: 1.7rem;
    color: white;
}

.contacts-list {
    margin-top: 1.7rem;
    margin-left:  0rem;
    display: flex;
    flex-wrap: wrap;
    grid-gap: 1rem;
    list-style: none;
    font-family: "Montserrat", Arial, Helvetica, sans-serif;
    color: WHITE;
}

.email-phone-list, .socials-list {
    margin-left:  0rem;
    width: 20rem;
    display: flex;
    flex-wrap: wrap;
    grid-gap: 1rem;
    list-style: none;
    font-family: "Montserrat", Arial, Helvetica, sans-serif;
    font-size: 1.2rem;
    color: WHITE;
}

.contact-type {
    font-style: normal;
    font-weight: 600;
    color:#5CE1E6;
}

.contact-value {
    color: white;
    font-style: normal;
}

.link {
    text-decoration-color: #5CE1E6;
}


@media screen and (max-width:800px) {
    .contacts-container {
        width: calc(100% - 3rem);
        height: auto;
        position: relative;
        border-top-color: #5CE1E6;
        border-top-width: 4px;
        border-top-style: solid;
        left: 1.5rem;
        padding-top: 1.5rem;
        margin-bottom: 4rem;
        overflow: hidden;
    }
}
